import React, { useState, useEffect } from 'react';
import CryptoJS from 'crypto-js';
import { useParams } from 'react-router-dom';
import { db, doc, getDoc } from './firebase'; // Firebase   

const App = () => {
  const [secretKey, setSecretKey] = useState('your-secret-key'); //  
  const [decryptedUid, setDecryptedUid] = useState('');
  const [documentData, setDocumentData] = useState(null); // Firestore   
  const { encryptedUid } = useParams(); // URL    

  useEffect(() => {
    if (encryptedUid) {
      const decodedEncryptedUid = decodeURIComponent(encryptedUid);
      decryptUid(decodedEncryptedUid);
    }
  }, [encryptedUid]);

  //  
  const decryptUid = (encryptedValue) => {
    const currentTime = new Date().toISOString(); //  
    try {
      const bytes = CryptoJS.AES.decrypt(encryptedValue, secretKey);
      const decryptedData = JSON.parse(bytes.toString(CryptoJS.enc.Utf8));

      const decryptedTimestamp = new Date(decryptedData.timestamp);
      const currentTimestamp = new Date();
      const timeDiff = Math.abs(currentTimestamp - decryptedTimestamp);
      const oneHour = 60 * 60 * 1000; // 1  

      if (timeDiff <= oneHour) {
        setDecryptedUid(decryptedData.uid);
        fetchDocument(decryptedData.uid); // UID Firestore  
      } else {
        setDecryptedUid('  ');
      }
    } catch (error) {
      setDecryptedUid(' ');
    }
  };

  // Firestore  
  const fetchDocument = async (uid) => {
    try {
      const docRef = doc(db, 'users', uid); //  ID UID 
      const docSnap = await getDoc(docRef);

      if (docSnap.exists()) {
        setDocumentData(docSnap.data());
      } else {
        setDocumentData(null);
      }
    } catch (error) {
      console.error('Firestore  :', error);
    }
  };

  return (
    <div>
      <h1>UID   Firestore </h1>
      <p><strong> UID:</strong> {decodeURIComponent(encryptedUid)}</p>
      <p><strong> UID:</strong> {decryptedUid}</p>
      {documentData && (
        <div>
          <h2> </h2>
          <pre>{JSON.stringify(documentData, null, 2)}</pre>
        </div>
      )}
      {!documentData && decryptedUid && decryptedUid !== '  ' && <p>  .</p>}
    </div>
  );
};

export default App;
